window.onload = function () {

  let body = document.body
  let textsElements = document.getElementsByClassName('should-change-color')
  let menuLinks = document.getElementsByClassName('animate-link')

  let anchors = document.getElementsByClassName('anchors')

  let menu1 = document.getElementById('open-btn-light')
  let menu2 = document.getElementById('open-btn-dark')
  let globe1 = document.getElementById('globe-light')
  let globe2 = document.getElementById('globe-dark')
  let sun = document.getElementById('sun')
  let moon = document.getElementById('moon')
  let lightText = document.getElementById('light-text')
  let darkText = document.getElementById('dark-text')

  let i;

  if (body.classList.contains('body-light')) {
    for (i= 0; i < textsElements.length; i++) {
      let textsElement = textsElements[i]
      textsElement.classList.add('text-black')
    }
    for (i= 0; i < textsElements.length; i++) {
      let textsElement = textsElements[i]
      textsElement.classList.remove('text-white')
    }
  } else if (body.classList.contains('body-dark')) {
    for (i= 0; i < textsElements.length; i++) {
      let textsElement = textsElements[i]
      textsElement.classList.add('text-white')
    }
    for (i= 0; i < textsElements.length; i++) {
      let textsElement = textsElements[i]
      textsElement.classList.remove('text-black')
    }
    
    for (i=0; i < menuLinks.length; i++) {
      let menuLink = menuLinks[i]
      menuLink.classList.add('menu-link')
    }

    menu1.style.display = "none"
    menu2.style.display = "block"
    globe1.style.display = "none"
    globe2.style.display = "block"
    moon.style.display = "none"
    sun.style.display = "block"
  } else {
    body.classList.add('body-light')
    for (i= 0; i < textsElements.length; i++) {
      let textsElement = textsElements[i]
      textsElement.classList.add('text-black')
    }
    for (i= 0; i < textsElements.length; i++) {
      let textsElement = textsElements[i]
      textsElement.classList.remove('text-white')
    }
  }





  for (i =0; i <anchors.length; i++) {
    let anchor = anchors[i]
    anchor.onclick = function () {

      if (body.classList.contains('body-light')) {
        sun.style.display = 'block'
        lightText.style.display = 'block'

        moon.style.display = 'none'
        darkText.style.display = 'none'

        body.classList.remove('body-light')
        body.classList.add('body-dark')

        for (i= 0; i < textsElements.length; i++) {
          let textsElement = textsElements[i]
          textsElement.classList.add('text-white')
        }
        for (i= 0; i < textsElements.length; i++) {
          let textsElement = textsElements[i]
          textsElement.classList.remove('text-black')
        }

        for (i=0; i < menuLinks.length; i++) {
          let menuLink = menuLinks[i]
          menuLink.classList.add('menu-link')
        }

        menu1.style.display = "none"
        menu2.style.display = "block"
        globe1.style.display = "none"
        globe2.style.display = "block"
        moon.style.display = "none"
        sun.style.display = "block"

      } else if (body.classList.contains('body-dark')) {
        moon.style.display = 'block'
        darkText.style.display = 'block'

        sun.style.display = 'none'
        lightText.style.display = 'none'

        body.classList.remove('body-dark')
        body.classList.add('body-light')

        for (i= 0; i < textsElements.length; i++) {
          let textsElement = textsElements[i]
          textsElement.classList.add('text-black')
        }
        for (i= 0; i < textsElements.length; i++) {
          let textsElement = textsElements[i]
          textsElement.classList.remove('text-white')
        }

        for (i=0; i < menuLinks.length; i++) {
          let menuLink = menuLinks[i]
          menuLink.classList.remove('menu-link')
        }

        menu1.style.display = "block"
        menu2.style.display = "none"
        globe1.style.display = "block"
        globe2.style.display = "none"
        moon.style.display = "block"
        sun.style.display = "none"
      }


      

    }
  }

}












